@import '../../assets/styles/fonts';
@import '../../assets/styles/mixins';
@import '../../assets/styles/variables';

.block-text {
    max-width: 600px;
    h2 {
        font-size: 4rem;
        line-height: 4rem;
        margin-bottom: 1.2rem;
        .starbucks-txt {
            color: $color_green;
            font-size: 4.5rem;
            font-weight: 900;
        }
    }
    a {
        display: inline-block;
        margin-top: 20px;
        padding: 8px 20px;
        background-color: $color_green;
        color:$color_white;
        text-decoration: none;
        border-radius: 40px;
        font-weight: 500;
        letter-spacing:1px;
    }
}

.block-img {
    display: flex;
    width: 100%;
    justify-content: flex-end;

    img {
        max-width: 340px;
    }
}

.thumb {
    display: flex;
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);


   
   
    li {
        list-style: none; 
        display: inline-block;
        margin: 0 20px;
        cursor: pointer;
        transition: 0.5s;

        img {
            max-width: 60px;
        }

        &:hover {
            transform: translateY(-15px);
        }

       
    }
}

.social {
    position: absolute;
    top: 50%;
    right: 30px;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
    li {
        list-style: none;
        margin: 3px 0;
        transform: scale(0.6);
        filter:invert(1);
    }

}

.circle {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: $color_green;
    clip-path: circle(600px at right 800px);
    z-index:-999;
}